@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap);
*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.container {
  max-width: 900px;
  margin: 0 auto;
}

.svg {
  transition: all 0.2s linear;
}

.close:hover {
  cursor: pointer;
  transform: scale(1.3);
}

.diagram {
  display: grid;
  grid-template-columns: 600px 1fr;
}
.test {
  width: 20px;
  height: auto;
  background-color: black;
}
.box-pointer-hidden {
  display: none;
}
@-webkit-keyframes render {
  0% {
    stroke-dasharray: 0 100;
  }
}
@keyframes render {
  0% {
    stroke-dasharray: 0 100;
  }
}

.diagram__pie svg {
  min-width: 500px;
  height: 500px;
  background: #ffffff;
  border-radius: 10%;
}

.diagram__pie-chart {
  fill: none;
  position: relative;
  transition: all 0.3s linear;
  -webkit-animation-name: render;
          animation-name: render;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  transition-property: all;
  transition-duration: 0.2s;
}
.diagram__pie-text {
  position: absolute;
  z-index: 9999;
  display: none;
}
.diagram__pie-chart:hover,
.hovered {
  stroke-width: 8;
}
.diagram__list {
  padding: 50px 0;
}
.diagram__item {
  list-style: none;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 20px;
}

.diagram__item > * {
  display: inline-flex;
}
.hovered .diagram__item-icon-diagram {
  transform: scale(1.4);
}
.diagram__item-icon-diagram {
  transition: all 0.3s linear;
  margin-right: 20px;
}
.diagram__item-name {
  margin-right: 5px;
  cursor: pointer;
}
.diagram__item-name,
.diagram__item-count {
  font-size: 20px;
  font-weight: 600;
}
.diagram__item-icon-close {
  margin-left: auto;
}
.diagram__text {
  font: 16px/1.4em "Montserrat", Arial, sans-serif;
  fill: #000;
  transform: translateY(0.25em);
  opacity: 0;
  transition: all 0.1s linear;
}
.diagram__text-count {
  font-size: 8px;
  line-height: 1;
  text-anchor: middle;
  transform: translateY(-0.25em);
}
.diagram__text-name {
  font-size: 2px;
  text-transform: uppercase;
  text-anchor: middle;
  transform: translateY(0.7em);
}

@-webkit-keyframes render2 {
  0% {
    fill-opacity: 0;
  }
}
@keyframes render2 {
  0% {
    fill-opacity: 0;
  }
}
#holder {
  display: flex;
  justify-content: center;
}
.diagram2 {
  position: relative;
}
.diagram2__g {
  position: absolute;
  top: 50%;
}
.diagram2__pie-chart > path {
  -webkit-animation-name: render2;
          animation-name: render2;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  transition-property: all;
  transition-duration: 0.2s;
}
.test {
  font-family: verdana, arial, sans-serif;
  font-size: 14;
  fill: black;
  stroke: none;
}
.test2 {
  stroke-linejoin: mitre;
  stroke-width: 2;
  stroke: black;
  fill: #ff0000;
}

.addItem {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.list {
  margin-bottom: 1vh;
  grid-gap: 1vh;
  display: grid;
}

.list__item {
  list-style: none;
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(2, 1fr) 20px 30px;
  grid-gap: 1vw;
}
.list__item > *,
.list__item-btn > * {
  transition: all 0.2s linear;
}
.list__item > *:not(:last-child):hover,
.list__item-btn > *:hover {
  transform: scale(1.1);
}
.list__item-input {
  padding: 5px 10px;
  outline: 0;
  border: 1px solid #cccccc;
}
.list__item-btn {
  display: flex;
  justify-content: space-around;
}
.btn {
  border: 0;
  border-radius: 5px;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 700;
  padding: 5px 10px;
}
.list__item-del {
  border: 0;
  background: none;
  outline: 0;
}

.warning {
  font-size: 14px;
  padding: 20px;
  border: 1px solid red;
}

.nav {
  margin-bottom: 1vh;
}

.nav__list {
  display: flex;
  justify-content: space-evenly;
}
.nav__item {
  text-transform: uppercase;
  list-style: none;
  width: 49%;
  transition: all 0.2s linear;
  border-bottom: 1px solid #cccccc;
  border-top: 1px solid #ffffff;
  border-radius: 5px;
  background-color: #ffffff;
}
.nav__item:hover {
  box-shadow: 10px 17.321px 40px 0px rgba(107, 125, 131, 0.3);
  transform: scale(1.1);
  border-top: 1px solid #cccccc;
}
.nav__item-link {
  text-align: center;
  transition: all 0.2s linear;
  text-decoration: none;
  color: #000000;
  padding: 10px 20px;
  font-size: 20px;
  display: block;
  font-weight: 700;
}
/* .nav__item-link:not(:last-child) {
  margin-right: 10px;
} */

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

